@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

html, body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  background: #2d2d2d;
  color: #fff;
  font-family: "Inter", sans-serif;
  overflow-x: hidden; /* Prevent horizontal scroll */
}

body {
  position: relative; /* Ensure proper stacking context */
}

.App {
  display: flex;
  width: 100%;
  min-height: 100vh;
  justify-content: center;
  background: #2d2d2d;
  position: relative; /* Create new stacking context */
}

.overlay.active {
  opacity: 1;
  visibility: visible;
}

/* Hamburger button styles */
.hamburger-button {
  display: none;
  position: fixed;
  bottom: 16px;
  left: 16px;
  width: 48px;
  height: 48px;
  flex-direction: column;
  row-gap: 5px;
  justify-content: center;
  border: none;
  cursor: pointer;
  z-index: 1000;
  padding: 12px;
  border-radius: 24px;
  background: #ffffff20;
  background-image: url('assets/bgnoise.png');
  box-shadow: 0 0 3px #2d2d2d20;
  backdrop-filter: blur(16px);
  transform: none !important; /* Prevent any transform effects */
  will-change: auto; /* Reset any performance optimizations */
}

.hamburger-button span {
  width: 100%;
  height: 2px;
  background: #fff;
}

.hamburger-button.open {
  background: transparent;
  backdrop-filter: none;
}

@media (max-width: 800px) {
  .sidebar {
    position: fixed;
    left: -100%;
    transition: 0.3s ease-out;
    display: flex;
    align-items: flex-end;
    background: #2d2d2d33;
    border-right: 1px solid rgba(255,255,255, .2);
    background-image: url('assets/bgnoise.png');
    backdrop-filter: blur(32px);
  }

  .sidebar.open {
    left: 0;
    transition: 0.3s ease-out;
  }

  .hamburger-button {
    display: flex;
  }

  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #2d2d2d80;
    z-index: 998;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-out, visibility 0.3s ease-out;
  }
}

.sidebar {
  width: 260px;
  height: 100vh;
  overflow: auto;
  display: flex;
  align-items: center;
  z-index: 999;
}

.menu {
  position: fixed;
  height: 288px;
  display: flex;
  padding: 24px 16px 64px;
  column-gap: 8px;
  z-index: 999;
}

.logoname {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 8px;
  padding-top: 56px;
}

.name {
  height: 32px;
  line-height: 32px;
}

.sidebarlinks {
  display: flex;
  flex-direction: column;
  padding-top: 128px;
  transition: all 0.3s ease-out;
}

.sidebarlinks a {
  opacity: 0.8;
  height: 32px;
  line-height: 32px; /* align the text vertically in the middle */
  cursor: pointer;
}

.sidebarlinks a.active {
  opacity: 1;
  font-weight: bold;
}

.sidebarlinks a:hover {
  cursor: pointer;
  opacity: 1;
}

.main-content {
  width: 100%;
  max-width: 800px;
}

.section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  position: relative;
}

.section-separator {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 2px;
  background: white;
  transition: width 0.1s ease-out;
  will-change: width;
}

@media (max-width: 768px) {
  .section-separator {
    max-width: 400px;
  }
}

@media (min-width: 769px) {
  .section-separator {
    max-width: 500px;
  }
}

.centered-container {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 800px;
  padding: 0 20px;
}

.lottie-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 500px;
  height: 313px;
}

@media (max-width: 768px) {
  .lottie-container {
    width: 400px;
    height: 400px;
  }
}

.tags-container {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.5s ease;
}

.tags-container.visible {
  opacity: 1;
}

.tag {
  padding: 4px 12px;
  font-size: 14px;
  border-radius: 4px;
  background: #ffffff20;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background-image: url('assets/bgnoise.png');
  backdrop-filter: blur(16px);
  display: flex;
  align-items: center;
  gap: 6px;
  text-decoration: none;
  transition: all 0.2s ease;
}

.tag-icon {
  color: inherit;
  flex-shrink: 0;
}

/* Warning tag style */
.tag-warning {
  background: rgba(255, 190, 118, 0.2);
  border-color: #ffbe76;
  color: #ffbe76;
}

.tag-warning:hover {
  background: rgba(255, 190, 118, 0.1);
}

/* Link tag style */
.tag-link {
  border-color: #c7ecee;
  background: rgba(199, 236, 238, 0.2);
  color: #c7ecee;
  cursor: pointer;
}

.tag-link:hover {
  background: rgba(199, 236, 238, 0.1);
}

